.pricing-container__item-btn {
  position: relative;
}

.pricing-container__btn {
  position: relative;
  z-index: 10;
  display: inline-block;
  width: 80%;
}

.pricing-container__btn--width {
  width: 100%;
}

.pricing-container__btn--not-active {
  color: #000;
  background-color: transparent;
}

.main-form {
  height: 100vh;
  background: rgba(255, 160, 57, 0.1);
  padding: 0 1rem;
}

.main-form__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.hero__box--hidden-active {
  display: none;
}

@media only screen and (min-width: 1024px) {
  .hero__box--hidden-active {
    display: flex;
  }
}
.form__input {
  padding-bottom: 1rem;
}

.input__box {
  width: 100%;
  border: 0;
  padding: 14px 0 15px 17px;
  margin-bottom: 1rem;
  background-color: #fafafa;
  border-radius: 15px;
  border: 1px solid #999898;
}

.form__btn {
  display: inline-block;
  padding-bottom: 1.5rem;
}

.form__btn--width {
  flex: 1;
  position: relative;
  z-index: 10;
  margin-bottom: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__checkbox {
  display: flex;
  align-items: center;
  padding-bottom: 2rem;
}

.form__checkbox--remember-me {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
}
.form__checkbox--remember-me .paddnig-l {
  padding-left: 35px;
  display: block;
}

/* Hide the browser's default checkbox */
.form__checkbox--remember-me input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark-checked {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  z-index: 10;
}

.checkmark-box {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  z-index: 11;
  bottom: 0;
  right: 0;
}

.form__checkbox--remember-me input:checked ~ .checkmark-box {
  fill: transparent;
  display: none;
}

.form-action__container {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding-bottom: 1rem;
}

.hide {
  display: none;
}

@media only screen and (min-width: 1024px) {
  .hide {
    display: inline-block;
  }
}
@media only screen and (min-width: 1366px) {
  .hide {
    display: none;
  }
}
::-webkit-input-placeholder {
  /* Edge */
  color: #2d2d2d;
}

::placeholder {
  color: #2d2d2d;
}